@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=NordiquePro-Bold:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bungee+Outline:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-roboto: Roboto;
  --font-poppins: Poppins;
  --font-nordiquepro-bold: NordiquePro-Bold;
  --font-rubik: Rubik;
  --font-inter: Inter;
  --font-bungee-outline: "Bungee Outline";

  /* font sizes */
  --font-size-base: 16px;
  --font-size-mini: 15px;
  --font-size-11xl: 30px;
  --font-size-lg: 18px;
  --font-size-6xl: 25px;
  --font-size-36xl: 55px;
  --font-size-21xl: 40px;
  --font-size-xl: 20px;
  --font-size-sm: 14px;
  --font-size-3xs: 10px;
  --font-size-31xl: 50px;
  --font-size-smi: 13px;
  --font-size-61xl: 80px;
  --font-size-42xl: 61px;

  /* Colors */
  --m3-sys-light-on-surface-variant: #49454f;
  --color-yellow: #e0ff22;
  --white: #fff;
  --color-gainsboro-100: #dfdfde;
  --color-gainsboro-200: #dedede;
  --modal-grey-1: #dbdad7;
  --color-gainsboro-300: rgba(217, 217, 217, 0);
  --color-black: #000;
  --primary-black-1: #1e1e1e;
  --color-slateblue-100: #5c59c0;
  --color-slateblue-200: #5b56bd;
  --color-wheat: #e7caa2;
  --color-powderblue: #9ad3cc;
  --color-darkgray: #b3acac;
  --primary-blue-1: #2a2aff;
  --color-mediumslateblue: #8764e3;
  --color-lightblue: #b4e0ff;
  --color-steelblue: #92a7de;
  --color-silver: #bcbcbc;

  /* Gaps */
  --gap-9xs: 4px;
  --gap-mini: 15px;
  --gap-62xl: 81px;
  --gap-3xs: 10px;
  --gap-10xs: 3px;
  --gap-smi: 13px;
  --gap-8xs: 5px;
  --gap-2xl: 21px;

  /* Paddings */
  --padding-5xs: 8px;
  --padding-9xs: 4px;
  --padding-xl: 20px;
  --padding-182xl: 201px;
  --padding-base: 16px;
  --padding-11xl: 30px;

  /* Border radiuses */
  --br-81xl: 100px;
  --br-115xl: 134px;
  --br-9xs: 4px;
  --br-3xs: 10px;
  --br-2xl: 21px;
  --br-31xl: 50px;
  --br-7xs: 6px;
  --br-sm: 14px;
  --br-3xl: 22px;
}
