.signUp,
.signUpWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.signUp {
  position: relative;
  text-transform: uppercase;
  font-weight: 500;
  width: 112px;
  height: 20px;
  flex-shrink: 0;
}
.signUpWrapper {
  position: absolute;
  height: 12.23%;
  width: 40.94%;
  top: 87.77%;
  right: 0;
  bottom: 0;
  left: 59.06%;
  border-radius: var(--br-9xs);
  background-color: var(--color-yellow);
  flex-direction: column;
  text-align: center;
  font-size: var(--font-size-mini);
  color: var(--primary-black-1);
  font-family: var(--font-poppins);
}
