.componentChild,
.yourEMail {
  position: relative;
}
.componentChild {
  border: 1px solid var(--white);
  background-color: transparent;
  box-sizing: border-box;
  width: 320px;
  height: 45px;
}
.yourEMailParent {
  position: absolute;
  top: 205px;
  left: 149px;
  width: 320px;
  height: 71px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-10xs);
  text-align: left;
  font-size: var(--font-size-mini);
  color: var(--white);
  font-family: var(--font-poppins);
}
