.readMore,
.readMoreWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.readMore {
  position: relative;
  text-transform: uppercase;
  font-weight: 600;
  width: 153px;
  height: 20px;
  flex-shrink: 0;
}
.readMoreWrapper {
  position: absolute;
  height: 1.1%;
  width: 12.63%;
  top: 36.54%;
  right: 48.08%;
  bottom: 62.37%;
  left: 39.29%;
  border-radius: var(--br-9xs);
  background-color: var(--color-yellow);
  flex-direction: column;
  text-align: center;
  font-size: var(--font-size-mini);
  color: var(--primary-black-1);
  font-family: var(--font-poppins);
}
