.supportingText {
  position: relative;
  letter-spacing: 0.5px;
  line-height: 24px;
}
.content {
  align-self: stretch;
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.iconsmoreVert24px {
  position: relative;
  width: 24px;
  height: 24px;
}
.stateLayer1 {
  display: flex;
  flex-direction: row;
  padding: var(--padding-5xs);
}
.container,
.ndTrailingIcon,
.stateLayer1 {
  align-items: center;
  justify-content: center;
}
.container {
  border-radius: var(--br-81xl);
  overflow: hidden;
  display: flex;
  flex-direction: row;
}
.ndTrailingIcon {
  width: 48px;
  height: 48px;
  display: none;
  flex-direction: column;
}
.stateLayer,
.stateenabledShowAvatarfal,
.trailingElements {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}
.stateLayer,
.stateenabledShowAvatarfal {
  justify-content: flex-start;
}
.stateLayer {
  align-self: stretch;
  flex: 1;
  padding: var(--padding-9xs);
  gap: var(--gap-9xs);
}
.stateenabledShowAvatarfal {
  border-radius: 28px;
  background: linear-gradient(
      rgba(113, 90, 174, 0.11),
      rgba(113, 90, 174, 0.11)
    ),
    #fffbfe;
  width: 328px;
  height: 56px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--m3-sys-light-on-surface-variant);
  font-family: var(--font-roboto);
}
